h4 {
  display: inline-block;
  margin: 2px;
}

.label {
  cursor: pointer;
}

.glyphicon {
  -webkit-transition: color 0.5s;
  -o-transition: color 0.5s;
  transition: color 0.5s;
}

.category-list-body {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  max-height: 500px;
  overflow-y: scroll;
}



